.examples-container {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.examples-container .examples-header {
  margin-bottom: 1.5rem;
}
.examples-container .examples-header .examples-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.examples-container .examples-header .examples-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
}
.examples-container .examples-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
.examples-container .examples-grid .example-card {
  padding: 1rem;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
  background: #f8fafc;
}
.examples-container .examples-grid .example-card:hover {
  border-color: #3b82f6;
  background: white;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: translateY(-1px);
}
.examples-container .examples-grid .example-card .example-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.75rem;
}
.examples-container .examples-grid .example-card .example-header .example-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
  background: #e5e7eb;
  color: #6b7280;
}
.examples-container .examples-grid .example-card .example-header .example-category {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.examples-container .examples-grid .example-card .example-header .example-category.badge-maintenance {
  background: #dbeafe;
  color: #1e40af;
}
.examples-container .examples-grid .example-card .example-header .example-category.badge-financial {
  background: #dcfce7;
  color: #166534;
}
.examples-container .examples-grid .example-card .example-header .example-category.badge-analytics {
  background: #f3e8ff;
  color: #7c3aed;
}
.examples-container .examples-grid .example-card .example-header .example-category.badge-search {
  background: #fed7aa;
  color: #c2410c;
}
.examples-container .examples-grid .example-card .example-header .example-category.badge-operations {
  background: #fecaca;
  color: #dc2626;
}
.examples-container .examples-grid .example-card .example-content {
  margin-bottom: 0.75rem;
}
.examples-container .examples-grid .example-card .example-content .example-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 0.25rem 0;
}
.examples-container .examples-grid .example-card .example-content .example-description {
  font-size: 0.75rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.4;
}
.examples-container .examples-grid .example-card .example-query {
  font-size: 0.75rem;
  color: #374151;
  font-style: italic;
  background: rgba(0, 0, 0, 0.05);
  padding: 0.5rem;
  border-radius: 0.375rem;
  border-left: 3px solid #3b82f6;
}
.examples-container .examples-grid .example-card.category-maintenance {
  border-left: 3px solid #3b82f6;
}
.examples-container .examples-grid .example-card.category-maintenance .example-icon {
  background: #dbeafe;
  color: #1e40af;
}
.examples-container .examples-grid .example-card.category-financial {
  border-left: 3px solid #10b981;
}
.examples-container .examples-grid .example-card.category-financial .example-icon {
  background: #dcfce7;
  color: #166534;
}
.examples-container .examples-grid .example-card.category-analytics {
  border-left: 3px solid #8b5cf6;
}
.examples-container .examples-grid .example-card.category-analytics .example-icon {
  background: #f3e8ff;
  color: #7c3aed;
}
.examples-container .examples-grid .example-card.category-search {
  border-left: 3px solid #f59e0b;
}
.examples-container .examples-grid .example-card.category-search .example-icon {
  background: #fed7aa;
  color: #c2410c;
}
.examples-container .examples-grid .example-card.category-operations {
  border-left: 3px solid #ef4444;
}
.examples-container .examples-grid .example-card.category-operations .example-icon {
  background: #fecaca;
  color: #dc2626;
}

@media (max-width: 768px) {
  .examples-container {
    padding: 1rem;
  }
  .examples-container .examples-grid .example-card {
    padding: 0.75rem;
  }
  .examples-container .examples-grid .example-card .example-header {
    margin-bottom: 0.5rem;
  }
  .examples-container .examples-grid .example-card .example-header .example-icon {
    width: 1.75rem;
    height: 1.75rem;
  }
  .examples-container .examples-grid .example-card .example-content {
    margin-bottom: 0.5rem;
  }
}
